/* materialize-css v1.0.0  */
/* @import 'https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0/css/materialize.min.css'; */
/* Bootstrap v4.2.1 */
@import 'https://stackpath.bootstrapcdn.com/bootstrap/4.2.1/css/bootstrap.min.css';
/* fontawesome icons v5.5.0 */
@import 'https://use.fontawesome.com/releases/v5.5.0/css/all.css';
/* google web fonts */
@import url('https://fonts.googleapis.com/css?family=Prompt|Courgette|PT+Sans+Caption');

/* custom styles */
body {
  margin: 0;
  padding: 0;
  font-family: "Prompt", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
