/* #region helper classes */
.hidden {
  display: none !important;
}
.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.flex-center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.flex-between {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.flex-around {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}
.flex-around-top {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.flex-b-25 {
  -webkit-flex-basis: 25%;
          flex-basis: 25%;
}
.flex-b-50 {
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
}
.flex-b-75 {
  -webkit-flex-basis: 75%;
          flex-basis: 75%;
}
.flex-b-5 {
  -webkit-flex-basis: 5%;
          flex-basis: 5%;
}
.flex-b-10 {
  -webkit-flex-basis: 10%;
          flex-basis: 10%;
}
.flex-b-20 {
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
}
.flex-b-30 {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
}
.flex-b-40 {
  -webkit-flex-basis: 40%;
          flex-basis: 40%;
}
.flex-b-60 {
  -webkit-flex-basis: 60%;
          flex-basis: 60%;
}
.pointer {
  cursor: pointer;
}
.card-header {
  padding: 1.2rem 1rem;
}
.sm {
  font-size: 2.5rem !important;
  line-height: inherit !important;
}
.md {
  font-size: 4rem !important;
  line-height: inherit !important;
}
.lg {
  font-size: 6rem !important;
  line-height: inherit !important;
}
.p-rl-1 {
  padding: 0 1rem;
}
.p-rl-2 {
  padding: 0 2rem;
}
.p-rl-3 {
  padding: 0 3rem;
}
.p-tp-1 {
  padding: 1rem 0;
}
.p-tp-2 {
  padding: 2rem 0;
}
.p-tp-3 {
  padding: 3rem 0;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
/* #endregion */

/* #region scrollbar styles */
.scroll::-webkit-scrollbar {
  width: 4px;
}
.scroll:hover::-webkit-scrollbar {
  width: 6px;
}
.scroll::-webkit-scrollbar-track-piece {
  background-color:#6f91b6;
  border-radius: 5px;
}
.scroll::-webkit-scrollbar-thumb {
  background-color: #0e355c;
  border-radius: 5px;
}
/* .scroll::-webkit-scrollbar-button {
  background-color: #fff;
} */
/* .scroll::-webkit-scrollbar-track {
  background-color: #444;
  border-radius: 30px;
} */
/* #endregion */

/* #region custom styles */
header {
  position: fixed;
  width: 100vw;
  z-index: 999;
}
.navbar-brand img.app-logo {
  width: 2rem;
  margin-right: 0.7rem;
}
main {
  position: relative;
}
main > div.spinner-border {
  width: 5rem;
  height: 5rem;
}
div.book-item {
  height: 80vh;
  overflow-y: auto;
}
div.card:hover {
  box-shadow: 0 0 3rem #ccc;
}
div.card .card-img-top {
  border-radius: 0;
}
div.card .fa-star {
  font-size: 1.2rem;
  color: #ffe359;
  cursor: pointer;
}
.card-title {
  margin: 0 !important;
}
.form-check-input,
.form-check-label {
  cursor: pointer;
}
/* #endregion */

/* #region media query */

/* Large */
@media only screen and (min-width: 993px) {
  main {
    top: 5rem;
  }
}
/* Medium */
@media only screen and (max-width: 992px) {
  main {
    top: 12rem;
  }
}
/* Small and Extra small */
@media only screen and (max-width: 768px) {
  main {
    top: 15rem;
  }
}

/* #endregion */
